<script>
  import {queryAvgDayCount, queryCalSnapShot} from '@/config/api.gateway';
  import generateImage from './generateImage';

  export default {
    name: "index",
    data() {
      return {
        accountUserType: '',
        accountUserName: '',
        userName: '',
        accountUserCode: '',
        accountUserId: '',
        startDate: '',
        endDate: '',
        tableData1: [],
        tableData2: [],
        tableData3: [],
        checklistData: [],
        opDialog2: false,
        quoteGroupName: '均重账单计算-结果总览'
      }
    },
    props: {
      row: Object
    },
    methods: {
      dbClick(row, column, event) {
        let params = {
          accountUserType: this.accountUserType,
          accountUserCode: this.accountUserCode,
          accountUserName: this.userName,
          accountUserId: this.accountUserId,
          startDate: this.startDate,
          endDate: this.endDate,
        };
        switch (params.accountUserType) {
          case '客户':
            params.accountUserType = 0;
            break;
          case '承包区':
            params.accountUserType = 2;
            break;
          case '业务员':
            params.accountUserType = 4;
            break;
          case '下级网点':
            params.accountUserType = 99;
            break;
        }
        console.log(params);
        this.$emit('goto', params);
      },
      exportExcel() {
        this.opDialog2 = true;
      },
      initialization(row) {
        console.log('新的', row);
        if (row) {
          this.accountUserId = row.accountUserId;
          this.accountUserCode = row.accountUserCode;
          this.userName = row.accountUserName;
          this.accountUserType = row.accountUserType;
          this.accountUserName = row.accountUserName + row.accountUserCode;
          this.startDate = this.moment(row.startDate).format('YYYY-MM-DD');
          this.endDate = this.moment(row.endDate).format('YYYY-MM-DD');

          if (this.accountUserType === 0) {
            this.accountUserType = '客户';
          } else if (this.accountUserType === 4) {
            this.accountUserType = '业务员';
          } else if (this.accountUserType === 2) {
            this.accountUserType = '承包区';
          } else if (this.accountUserType === 99) {
            this.accountUserType = '下级网点';
          } else this.accountUserType = '';

          this.queryCount(row.id);
          this.queryShot(row.id);
        }
      },
      avgWayMode(row) {
        if (row.avgWayMode) {
          let avgWayMode = row.avgWayMode;
          if (avgWayMode === 1) {
            return '票数占比超出后取最大重量';
          } else if (avgWayMode === 2) {
            return '票数占比超出后取最高费用';
          } else if (avgWayMode === 3) {
            return '票数占比超出后加收';
          } else if (avgWayMode === 4) {
            return '重量占比超出后加收';
          } else return '';
        }
      },
      avgWayType(row) {
        if (row.avgWayType) {
          let avgWayType = row.avgWayType;
          if (avgWayType === 1) {
            return '重量占比';
          } else if (avgWayType === 2) {
            return '票数占比';
          } else return '';
        }
      },
      fmtPercent(row, column) {
        if (row[column.property] !== null || row[column.property] !== undefined)
          return row[column.property] + '%';
        else return '';
      },
      fmtFiex2(row, column) {
        if (row[column.property] === null || row[column.property] === undefined) return '';
        else {
          return row[column.property].toFixed(2);
        }
      },
      fmtFiex4(row, column) {
        if (row[column.property] === null || row[column.property] === undefined) return '';
        else {
          return row[column.property].toFixed(4);
        }
      },
      fmtBillDate(row) {
        return this.moment(row.billDate).format('YYYY-MM-DD')
      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '总账';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                if (prev !== 0 || prev !== null) prev = prev * 100;
                if (curr !== 0 || curr !== null) curr = curr * 100;
                if (column.property === 'billCount' || column.property === 'calculateCount') return (prev + curr) / 100;
                return ((prev + curr) / 100).toFixed(2);
              } else {
                return (prev * 1).toFixed(2);
              }
            }, 0);
            sums[index];
          }
        });
        return sums;
      },
      queryCount(calTaskId) {
        queryAvgDayCount({calTaskId: calTaskId}).then(res => { // 表格1
          // console.log('表格1', res);
          if (res.status === false || res.result.length === 0) {
            this.tableData1 = [];
            return
          }
          this.tableData1 = res.result;
        }).catch(err => {
          this.tableData1 = [];
        });
      },
      queryShot(calTaskId) {
        queryCalSnapShot({calTaskId: calTaskId}).then(res => { // 表格1
          // console.log('表格2、3', res);
          if (res.status === false || res.result.length === 0) {
            this.tableData2 = [];
            this.tableData3 = [];
            return
          }
          let data = res.result;
          this.tableData2 = data.avgAreaGroupSnapList;
          this.tableData3 = data.avgWayAreaSnapList;
        }).catch(err => {
          this.tableData2 = [];
          this.tableData3 = [];
        })
      }
    },
    computed: {
      proportion() {
        return this.tableData3.length === 0 ? false : true
      }
    },
    components: {generateImage}
  }
</script>

