<template>
  <main>
    <template v-if="openAvgWeight">

      <el-tabs type="border-card" v-model="activeName" @tab-click="onTabClick(activeName)">
        <el-tab-pane label="均重计算" name="均重计算">
          <calculation @getStatus="getCalculStatus" @goto="goto"/>
        </el-tab-pane>
        <el-tab-pane :disabled="calculStatus" label="结果总览" name="结果总览">
        <span v-if="calculStatus" slot="label">
          <el-tooltip class="item" effect="light" content="请先选中计算过的账单，再点击结果总览哦" placement="top">
            <div>结果总览</div>
          </el-tooltip>
        </span>
          <overview ref="overview" @goto="goto"/>
        </el-tab-pane>
        <el-tab-pane :disabled="calculStatus" label="单号明细" name="单号明细">
        <span v-if="calculStatus" slot="label">
          <el-tooltip class="item" effect="light" content="请先选中计算过的账单，再点击单号明细哦" placement="top">
            <div>单号明细</div>
          </el-tooltip>
        </span>
          <detailed ref='detailed'/>
        </el-tab-pane>
      </el-tabs>

    </template>
    <template v-else>
      <div class="tip">请先到如意系统的结算规则配置处开通均重账单权限!</div>
    </template>
  </main>
</template>
<script>
  import calculation from './calculation';
  import detailed from './detailed'
  import overview from './overview'

  export default {
    name: "checkInterchangeFee",
    data() {
      return {
        activeName: '均重计算',
        calculStatus: true,
        row: {}
      }
    },
    created() {
    },
    methods: {
      getCalculStatus(val, row) {
        if (row) this.row = row;
        this.calculStatus = val;
      },
      goto(row) {
        this.activeName = '单号明细';
        this.$refs.detailed.toDetailed(row);
        this.z_method.refreshSpread();
      },
      onTabClick(name) {
        if (name === '结果总览') {
          this.$refs.overview.initialization(this.row)
        } else if (name == '单号明细') {
          this.$refs.detailed.initialization(this.row);
          this.z_method.refreshSpread()
        }
      }
    },
    computed: {
      openAvgWeight() {
        return this.z_param.avgStatus
      }
    },
    components: {calculation, detailed, overview},
  }
</script>

<style scoped lang="scss">
  .tip {
    font-size: 32px;
    font-weight: bold;
    text-align: center;
    padding-top: 15%;
    color: #e35664;
  }
</style>
