<template>
    <main class="imageExport">
        <el-dialog
            :title="title"
            :modal-append-to-body="false"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            :show-close="false"
            :visible.sync="dialogVisibles"
            :width="width+'px'"
            center
        >
            <div :style="{height:height+'px'}" class="body-con">
              <div ref="imageDialog">
                <slot name="imageExportCon" />
              </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="handleAct" :loading="disableBtnLoading" :disabled="disableBtnLoading">下载保存</el-button>
            </span>
        </el-dialog>
    </main>
</template>
<script>
import html2canvas from 'html2canvas'
export default {
  name: 'generateImage',
  props: {
    dialogVisibles: {
      type: Boolean,
      default: false
    },
    title: {
      type: [String],
      default: '报价预览'
    },
    titleDowmload: {
      type: [String],
      default: '报价预览'
    },
    width: {
      type: [Number],
      default: 800
    },
    height: {
      type: [Number],
      default: 200
    }
  },
  data() {
    return {
      disableBtnLoading: false
    }
  },
  methods: {
    handleClose() {
      this.$emit('dealDialog')
    },
    handleAct() {
      this.disableBtnLoading = true
      html2canvas(this.$refs.imageDialog, { scale: 2 }).then(canvas => {
        // 转成图片，生成图片地址
        const imgUrl = canvas.toDataURL('image/png')

        var eleLink = document.createElement('a')
        eleLink.href = imgUrl // 转换后的图片地址
        eleLink.download = this.titleDowmload
        // 触发点击
        document.body.appendChild(eleLink)
        eleLink.click()
        // 然后移除
        document.body.removeChild(eleLink)
        this.disableBtnLoading = false
        this.handleClose()
      }).catch(() => {
        this.disableBtnLoading = false
        this.handleClose()
      })
    }
  }
}
</script>
<style lang="scss">
.imageExport{
  .body-con{
    overflow:auto
  }
    .el-dialog__headerbtn{
        top:18px !important;
    }
}
</style>
