<template>
  <div class="sz-main sendReceiveFeeDetail">
    <div class="sz-search" slot="collapse">
      <div style="display:inline-block;">
        <div style="display:inline-block;width:calc(100% );">
          <ul class="clear-both">
            <li>
              <label class="w-r-65">开始日期：</label>
              <sz-date-picker
                v-model="startDate"
                type="daterange"
                value-format="yyyy-MM-dd"
                range-separator="至"
                :clearable="false"
                :editable="false"
                :isCheckMsg="false"
                class="input-160"/>
            </li>

            <li>
              <label class="w-r-85">账单类型：</label>
              <el-select v-model="params.accountUserType">
                <el-option
                  v-for=" item in billTypes"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>

            <li>
              <label class="w-r-85">结算对象：</label>
              <sc-users type="7" v-model="params.accountUserId" class="input-h-85"/>
            </li>

            <li>
              <label class="w-r-85">计算状态：</label>
              <el-select v-model="params.calculStatus">
                <el-option
                  v-for=" item in computationals"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
          </ul>
        </div>
        <div style="display:inline-block;width:calc(100% - 74px);">
          <ul>
            <li>
              <label class="w-r-65">结束日期：</label>
              <sz-date-picker
                v-model="endDate"
                type="daterange"
                value-format="yyyy-MM-dd"
                range-separator="至"
                :clearable="true"
                :editable="false"
                :isCheckMsg="false"
                class="input-160"/>
            </li>

            <li>
              <label>应收费用为0票数：</label>
              <el-select v-model="params.payableZeroCountStatus">
                <el-option
                  v-for=" item in votes"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>

            <li>
              <el-checkbox v-model="params.isDifferentCount">实际票数和计算票数不一致</el-checkbox>
            </li>

            <li>
              <el-checkbox v-model="params.isDifferentWeight">实际重量和计算重量不一致</el-checkbox>
            </li>

          </ul>

        </div>
        <div style="display:inline-block;">
          <div></div>
          <el-button @click="query" type="primary" class="m-b-16" icon="el-icon-search">查询</el-button>
        </div>

      </div>

    </div>
    <div class="sz-search-btn" style="border-bottom:0px;">
      <div class="ulH">
        <ul>
          <li>
            <el-button type="primary" @click="openTasks">创建任务</el-button>
          </li>
          <li>
            <el-button type="danger" :disabled="isDel" @click="delTasks">删除任务</el-button>
          </li>
          <li>
            <el-button type="primary" :disabled="isDel" v-if="z_method.showBtn('btn_avgWeightSummary_calculate')"
                       @click="avWeight">计算均重
            </el-button>
          </li>
        </ul>
      </div>
      <div>
        <el-button type="primary" icon="el-icon-download" @click="exportExcel">导出</el-button>
      </div>
    </div>

    <!--表格-->
    <div class="sz-content">
      <el-table
        :height="tableH+'px'"
        ref="multipleTable"
        :data="tableData"
        show-summary
        :summary-method="getSummaries"
        border
        @row-dblclick="dbClick"
        tooltip-effect="dark">
        <el-table-column type="selection" width="50">
          <template slot-scope="scope">
            <el-tooltip v-if="scope.row.calculStatus===1||scope.row.countStatus===1" class="item" effect="dark"
                        content="该任务正在计算均重，请稍后再试！"
                        placement="top"><i class="el-icon-time"/>
            </el-tooltip>
            <el-radio v-model="radio" class="customize-radio" :label="scope.row.id" v-else
                      @change="getTableData(scope.row,$event)"/>
          </template>
        </el-table-column>
        <template v-for="(col,index) in tableHead.columns">
          <el-table-column :prop='col.name' :label="col.displayName" :width="col.width" :formatter="col.value"/>
        </template>
      </el-table>
    </div>
    <div class="sz-pagination-box">
      <el-pagination class="pagination"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page.sync="params.currentPage"
                     :page-sizes="[100, 200, 500, 1000,5000]"
                     :page-size="params.pageSize"
                     :total="params.total"
                     layout="total, sizes, prev, pager, next, jumper"/>
    </div>
    <!--创建任务-->
    <el-dialog
      :modal-append-to-body='false'
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title="创建任务"
      :visible.sync="createTasks"
      width="400px">
      <div class="tasks">
        <div v-if="createTasks">
          <label><span>*</span> 结算对象：</label>
          <sc-users type="7" @change="getTasksAccount" popper-class="max-index" v-model="taskParams.accountUserId"
                    class="input-h-85"/>
        </div>
        <div>
          <label><span>*</span> 开始日期：</label>
          <el-date-picker
            :editable="false"
            :clearable="false"
            v-model="taskParams.startDate"
            ref="timePicker"
            type="date">
          </el-date-picker>
        </div>
        <div>
          <label><span>*</span> 结束日期：</label>
          <el-date-picker
            :editable="false"
            :clearable="false"
            v-model="taskParams.endDate"
            ref="timePicker"
            type="date">
          </el-date-picker>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="createTasks = false">取 消</el-button>
        <el-button :disabled="isCreate" type="primary" @click="tasksSub">确 定</el-button>
      </span>
    </el-dialog>
    <verification :isError="isError" :titles="titles" :titleCon="titleCon" :dialogVisibles="dialogVisible"
                  @callBack="callBack"/>
    <!--导出-->
    <sc-csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"/>
  </div>
</template>

<script>
  import {
    createTasks,
    queryClaTasks,
    judgeIsCalculOver,
    executeCalStep1,
    executeCalStep2,
    deleteTask
  } from '@/config/api.gateway';
  import {cloneDeep, trim} from "lodash";
  import moment from 'moment';
  import verification from './verification';
  import {exportCsvUtils} from '@/utils/mixin';

  export default {
    mixins: [exportCsvUtils],
    data() {
      return {
        isError: true,
        radio: '',
        startDate: [this.moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"), this.moment().format("YYYY-MM-DD 00:00:00")],
        endDate: [],
        params: {
          accountUserType: -1, // 账单类型
          accountUserId: '', // 结算对象
          calculStatus: -1, // 计算状态
          payableZeroCountStatus: 0, // 应收费用为0票数
          isDifferentCount: false, // 实际票数和计算票数不一致
          isDifferentWeight: false, // 实际重量和计算重量不一致
          // 翻页
          total: 0,
          pageSize: 100,
          currentPage: 1,
        },

        taskParams: {
          accountUserId: '', // 结算对象
          startDate: this.moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"), // 开始日期
          endDate: this.moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"), // 结束日期
          accountUserType: '', // 结算对象类型
        },
        multipleSelection: [],
        tableData: [],
        tableH: 300,

        createTasks: false, // 创建任务弹框
        dialogVisible: false,
        titles: '',
        titleCon: '',
        tableHead: {
          columns: [
            {
              displayName: '开始日期', name: 'startDate', width: 100, value(row) {
                return moment(row.startDate).format('YYYY-MM-DD')
              }
            },
            {
              displayName: '结束日期', name: 'endDate', width: 100, value(row) {
                return moment(row.endDate).format('YYYY-MM-DD')
              }
            },
            {
              displayName: '账单类型', name: 'accountUserType', value(row) {
                if (row.accountUserType === 0) {
                  return '客户';
                } else if (row.accountUserType === 4) {
                  return '业务员';
                } else if (row.accountUserType === 2) {
                  return '承包区';
                } else if (row.accountUserType === 99) {
                  return '下级网点';
                } else return ''
              },
            },
            {displayName: '结算对象', name: 'accountUserName', width: 100},
            {displayName: '结算对象编码', name: 'accountUserCode', isCode: true, width: 110},
            {displayName: '实际票数', name: 'realCount',},
            {
              displayName: '实际重量', name: 'realWeight', isCode: true,
              value(row) {
                if (row.realWeight === undefined || row.realWeight === null) return '';
                else return row.realWeight.toFixed(2);
              }
            },
            {displayName: '计算票数', name: 'calculCount'},
            {
              displayName: '计算重量', name: 'calculWeight', isCode: true, value(row) {
                if (row.calculWeight === undefined || row.calculWeight === null) return '';
                else return row.calculWeight.toFixed(2);
              }
            },
            {
              displayName: '面单费', name: 'billFee', isCode: true, value(row) {
                if (row.billFee === undefined || row.billFee === null) return '';
                else return row.billFee.toFixed(2);
              }
            },
            {
              displayName: '增值加收费', name: 'valueAddedFee', width: 100, isCode: true, value(row) {
                if (row.valueAddedFee === undefined || row.valueAddedFee === null) return '';
                else return row.valueAddedFee.toFixed(2);
              }
            },
            {
              displayName: '通票费', name: 'ordinaryFee', isCode: true, value(row) {
                if (row.ordinaryFee === undefined || row.ordinaryFee === null) return '';
                else return row.ordinaryFee.toFixed(2);
              }
            },
            {
              displayName: '加收费', name: 'extraFee', isCode: true, value(row) {
                if (row.extraFee === undefined || row.extraFee === null) return '';
                else return row.extraFee.toFixed(2);
              }
            },
            {
              displayName: '应收费用', name: 'payable', isCode: true, value(row) {
                if (row.payable === undefined || row.payable === null) return '';
                else return row.payable.toFixed(2);
              }
            },
            {displayName: '应收费用为0票数', name: 'payableZeroCount', width: 120},
            {
              displayName: '计算状态', name: 'calculStatus', value(row) {
                if (row.calculStatus === 0) {
                  return '未计算';
                } else if (row.calculStatus === 1) {
                  return '计算中';
                } else if (row.calculStatus === 2) {
                  return '已计算';
                } else if (row.calculStatus === 3) {
                  return '计算失败';
                } else return ''
              },
            },
            {displayName: '创建人', name: 'create', width: 130},
            {
              displayName: '创建时间', name: 'gmtCreate', width: 100, value(row) {
                return moment(row.gmtCreate).format('YYYY-MM-DD')
              }
            },
            {displayName: '计算人', name: 'calculUser', width: 130},
            {
              displayName: '计算时间', name: 'calculTime', width: 140, value(row) {
                if (row.calculTime) return moment(row.calculTime).format('YYYY-MM-DD HH:mm:ss')
              }
            },
          ]
        },
        exportObj: { // 导出
          visible: false,
          sum: 1, // 一共获取数据的总次数
          current: 0, // 当前获取第几次数据
          fileName: '均重计算',
          excelData: [],
        },
        billTypes: [ // 账单类型
          {value: -1, label: "全部"},
          {value: 0, label: "客户"},
          {value: 4, label: "业务员"},
          {value: 2, label: "承包区"},
          {value: 99, label: "下级网点"}],
        computationals: [ // 计算状态
          {value: -1, label: "全部"},
          {value: 0, label: "未计算"},
          {value: 1, label: "计算中"},
          {value: 2, label: "已计算"},
          {value: 3, label: "计算失败"}],
        votes: [ // 应收费用为0票数
          {value: 0, label: "全部"},
          {value: 1, label: "=0"},
          {value: 2, label: ">0"}],
      }
    },
    methods: {
      query() {
        this.params.currentPage = 1;
        this.params.pageSize = 100;
        this.onQuery()
      },
      onQuery() {
        if (this.startDate) {
          let resultDate = (new Date(this.startDate[1]) - new Date(this.startDate[0])) / 1000 / 60 / 60 / 24;
          if (resultDate < 0) {
            return this.$message({
              message: '起始日期不能大于结束日期',
              type: 'warning'
            });
          }
          if (resultDate > 30) {
            return this.$message({
              message: '最多可查询31天的数据,请重新选择开始日期',
              showClose: false,
              type: 'warning'
            });
          }
        }
        let endDates = undefined;
        if (this.endDate[0] && this.endDate[1]) {
          let resultDate = (new Date(this.moment(this.endDate[1]).format("YYYY-MM-DD 00:00:00")) - new Date(this.moment(this.endDate[0]).format("YYYY-MM-DD 00:00:00"))) / 1000 / 60 / 60 / 24;
          if (resultDate < 0) {
            return this.$message({
              message: '开始日期不能大于结束日期',
              type: 'warning'
            });
          }
          if (resultDate > 30) {
            return this.$message({
              message: '最多可查询31天的数据,请重新选择结束日期',
              showClose: false,
              type: 'warning'
            });
          }
          endDates = [moment(this.endDate[0]).format('YYYY-MM-DD 00:00:00'), moment(this.endDate[1]).format('YYYY-MM-DD 00:00:00')];
        } else if (this.endDate[0] || this.endDate[1]) {
          return this.$message({
            message: '请选择正确的结束日期',
            showClose: false,
            type: 'warning'
          });
        }
        const {accountUserType, accountUserId, calculStatus, payableZeroCountStatus, isDifferentCount, isDifferentWeight, pageSize, currentPage} = this.params;
        let params = {
          startDates: [moment(this.startDate[0]).format('YYYY-MM-DD 00:00:00'), moment(this.startDate[1]).format('YYYY-MM-DD 00:00:00')],
          endDates: endDates,
          accountUserType: accountUserType,
          accountUserId: accountUserId !== '' ? accountUserId : undefined,
          calculStatus: calculStatus,
          payableZeroCountStatus: payableZeroCountStatus,
          isDifferentCount: isDifferentCount ? 1 : 0,
          isDifferentWeight: isDifferentWeight ? 1 : 0,
          pageSize: pageSize,
          currentPage: currentPage
        };
        queryClaTasks(params).then(res => {
          // console.log('查询', res);
          if (res.status === false || res.result.list.length === 0) {
            this.tableData = [];
            this.params.total = 0;
            this.params.currentPage = 1;
            this.params.pageSize = 100;
            return this.$message({
              message: '查询结果无数据',
              type: 'warning',
            });
          }
          this.$emit('getStatus', true);
          this.multipleSelection = [];
          this.radio = '';
          let data = res.result;
          this.tableData = data.list;
          this.params.total = data.totalRow;
        }).catch(err => {
          this.tableData = [];
          this.params.total = 0;
          this.params.currentPage = 1;
          this.params.pageSize = 100;
        })
      },
      avWeight() {
        if (this.multipleSelection.length !== 1) {
          return this.$message({
            type: 'warning',
            message: '请选择单条数据!'
          });
        }
        let params = {taskId: this.multipleSelection[0].id};
        executeCalStep1(params).then(res => {
          if (res.status === true) {
            judgeIsCalculOver(params).then(res => {
              if (res.result === true) {
                this.titles = '你是否确认重新计算该均重任务？';
                this.titleCon = '注意：新计算出的均重费用会覆盖掉原均重费用；';
                this.isError = false;
                this.dialogVisible = true;
              } else {
                this.$confirm('你是否确认计算该均重任务?', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning',
                  closeOnPressEscape: false,
                  showClose: false,
                  closeOnClickModal: false
                }).then(() => { // 确定回调
                  this.setp2(params)
                }).catch(() => { // 取消回调

                });
              }
            }).catch(err => {
            })
          } else if (res.status === false) {
            return this.$message({
              type: 'warning',
              message: res.message
            });
          }
        }).catch(err => {
        });
      },
      setp2(params) {
        executeCalStep2(params).then(res => {
          console.log('确定计算', res);
          if (res.status === true) {
            this.query();
            return this.$message({
              type: 'warning',
              message: '操作成功!'
            });
          }
        }).catch(err => {
        })
      },
      delTasks() {
        if (this.multipleSelection.length !== 1) {
          return this.$message({
            type: 'warning',
            message: '请选择单条数据!'
          });
        }
        let params = {taskId: this.multipleSelection[0].id};
        judgeIsCalculOver(params).then(res => {
          if (res.result === true) {
            this.titles = '你是否确认删除该任务？';
            this.titleCon = '注意：删除后，原计算出的均重费用不会清空。请及时创建计算均重，新计算出的均重费用会覆盖掉原均重费用；';
            this.isError = true;
            this.dialogVisible = true;
          } else {
            this.$confirm('你是否确认删除该任务?', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'error',
              closeOnPressEscape: false,
              showClose: false,
              closeOnClickModal: false
            }).then(() => { //  确定按钮回调
              this.deleteTasks(params)
            }).catch(() => { // 取消按钮回调

            });
          }
        }).catch(err => {
        });
      },
      deleteTasks(params) {
        deleteTask(params).then(res => {
          console.log('删除任务', res);
          if (res.status === true) {
            this.query();
            return this.$message({
              type: 'warning',
              message: '删除成功!'
            });
          }
        }).catch(err => {
        })
      },
      tasksSub() {
        if (this.taskParams.endDate && this.taskParams.startDate) {
          let resultDate = (new Date(this.taskParams.endDate) - new Date(this.taskParams.startDate)) / 1000 / 60 / 60 / 24;
          if (resultDate < 0) {
            return this.$message({
              message: '开始日期不能大于结束日期',
              type: 'warning'
            });
          }
          if (resultDate > 30) {
            return this.$message({
              message: '最多可查询31天的数据,请重新选择日期',
              showClose: false,
              type: 'warning'
            });
          }
        }

        let params = this.taskParams;
        params.taskType = 1;
        params.startDate = moment(params.startDate).format('YYYY-MM-DD 00:00:00');
        params.endDate = moment(params.endDate).format('YYYY-MM-DD 00:00:00');
        console.log(params, moment(params.startDate).format('YYYY-MM-DD'));
        createTasks(params).then(res => {
          if (res.status === false) {
            return this.$message({
              message: res.message,
              type: 'warning'
            });
          }
          this.query();
          this.createTasks = false;
          return this.$message({
            message: '创建任务成功',
            type: 'success'
          });
        });
      },
      exportExcel() {
        if (this.params.total === 0)
          return this.$message({
            message: '没有数据无法导出',
            type: 'warning'
          });
        let paramObj = {
          totalRow: this.params.total,
          checklistData: this.tableData,
          url: '/avg/claTaskIndex/queryClaTasks',
          paramIn: this.optionsClone,
          fileName: '均重计算',
          type: 'page',
          methodType: 'post',
        };
        this.exportBasicUtils(paramObj);
      },
      getTableData(row, val) {
        if (!isNaN(val)) this.multipleSelection = [row];
        if (row.payable === undefined || row.payable === null) {
          return this.$emit('getStatus', true);
        } else {
          return this.$emit('getStatus', false, row);
        }
      },
      dbClick(row, column, event) {
        if (row.payable === null || row.payable === undefined) return this.$emit('getStatus', true); // 不允许跳转可以弹出提示
        else {
          console.log(row.calculStatus, row.countStatus);
          if (row.calculStatus !== 1 || row.countStatus !== 1) this.multipleSelection = [row];
          this.radio = row.id;
          this.$emit('goto', row);
          return this.$emit('getStatus', false, row);
        }
      },
      callBack(Booleans) {
        if (Booleans && this.titles === '你是否确认删除该任务？') { // 删除任务 点击确定回调
          console.log('删除任务');
          console.log(this.multipleSelection[0]);
          this.deleteTasks({taskId: this.multipleSelection[0].id})
        } else if (Booleans && this.titles === '你是否确认重新计算该均重任务？') { // 计算均重 点击确定回调
          console.log('计算均重');
          console.log(this.multipleSelection[0]);
          this.setp2({taskId: this.multipleSelection[0].id})
        }
        this.dialogVisible = false;
      },
      getTasksAccount(val) {
        this.taskParams.accountUserType = val.type;
      },
      openTasks() {
        this.taskParams.accountUserId = '';
        this.taskParams.startDate = this.moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"); // 开始日期
        this.taskParams.endDate = this.moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"); // 开始日期
        this.createTasks = true;
      },
      handleSizeChange(size) {
        this.params.pageSize = size;
        this.handleCurrentChange(1);
      },
      handleCurrentChange(curPage) {
        this.params.currentPage = curPage;
        this.onQuery();
      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '汇总';
            return;
          }

          const values = data.map(item => {
            if (column.property === 'realCount' || column.property === 'realWeight' || column.property === 'calculCount' || column.property === 'calculWeight' || column.property === 'billFee' || column.property === 'valueAddedFee' || column.property === 'ordinaryFee' || column.property === 'extraFee' || column.property === 'payable' || column.property === 'payableZeroCount') {
              if (item[column.property] === null) item[column.property] = undefined;
              return Number(item[column.property])
            }
          });
          // console.log(values)
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                if (prev !== 0 || prev !== null) prev = prev * 100;
                if (curr !== 0 || curr !== null) curr = curr * 100;
                // return parseFloat(((prev + curr) / 100).toFixed(2));
                if (column.property === 'realCount' || column.property === 'calculCount' || column.property === 'payableZeroCount') return (prev + curr) / 100;
                return ((prev + curr) / 100).toFixed(2);
              } else {
                return (prev * 1).toFixed(2);
              }
            }, 0);
            sums[index];
          }
        });
        return sums;
      },
      closeDialog(val) { // 关闭导出框
        this.exportObj.visible = val;
        this.exportObj.current = 1;
      },
      toFix2(row, column, cellValue, index,) {
        if (row[column.property] === null || row[column.property] === undefined) return '';
        else {
          return row[column.property].toFixed(2);
        }
      },
      fmtStartDate(val) {
        return moment(val.startDate).format('YYYY-MM-DD')
      },
      fmtEndDate(val) {
        return moment(val.endDate).format('YYYY-MM-DD')
      },
      fmtAccountUserType(val) {
        if (val.accountUserType === 0) {
          return '客户';
        } else if (val.accountUserType === 4) {
          return '业务员';
        } else if (val.accountUserType === 2) {
          return '承包区';
        } else if (val.accountUserType === 99) {
          return '下级网点';
        } else return ''
      },
      fmtCalculStatus(val) {
        if (val.calculStatus === 0) {
          return '未计算';
        } else if (val.calculStatus === 1) {
          return '计算中';
        } else if (val.calculStatus === 2) {
          return '已计算';
        } else if (val.calculStatus === 3) {
          return '计算失败';
        } else return ''
      },
      fmtGmtCreate(val) {
        return moment(val.gmtCreate).format('YYYY-MM-DD HH:mm:ss')
      },
      fmtCalculTime(val) {
        if (val.calculTime === null) return '';
        else return moment(val.calculTime).format('YYYY-MM-DD HH:mm:ss')
      },
      onresize() {
        this.tableH = document.body.clientHeight - 250; // 根据用户页面布局修改相应高度
        this.z_method.refreshSpread(); // 刷新表格
      },
    },
    computed: {
      isCreate() {
        if (this.taskParams.accountUserId && this.taskParams.startDate && this.taskParams.endDate) return false;
        return true;
      },
      isDel() {
        if (this.multipleSelection.length !== 0) return false;
        else return true;
      }
    },
    mounted() {
      this.$nextTick(() => {
        document.querySelectorAll('.has-gutter .cell')[0].innerText = '选择'
      });
      window.addEventListener('resize', this.onresize);
    },
    created() {
      this.tableH = document.body.clientHeight - 250; // 根据用户页面布局修改相应高度
    },
    destroyed() {
      window.removeEventListener('resize', this.onresize);
    },
    components: {verification}
  };
</script>
