<template>
  <div class="sz-main sendReceiveFeeDetail">
    <show-more v-model="ishowHeights">

      <div class="sz-search" slot="collapse">

        <div style="display:inline-block;">
          <div style="float:left;margin-top:5px;">
            <sc-bill-code v-model="billCode" @typeChange="getType" :onlyBill="false"
                          ref="billcodeRef" @keyup.native="keyup"/>
          </div>
          <div>
            <ul class="clear-both " style="padding-left: 10px">
              <li>
                <label class="w-r-65">账单日期：</label>
                <sz-date-picker
                  :disabled="true"
                  v-model="dateValue"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  :clearable="false"
                  :editable="false"
                  :isCheckMsg="false"
                  class="input-160"/>
              </li>

              <li>
                <label class="w-r-85">账单类型：</label>
                <el-select v-model="params.type" :disabled="true">
                  <el-option
                    v-for=" item in types"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </li>

              <li>
                <label class="w-r-85">结算对象：</label>
                <el-input v-model="accountUserName" :disabled="true" class="input-h-85"/>
              </li>

              <li>
                <label class="w-r-85">面单类型：</label>
                <el-select v-model="params.billType">
                  <el-option
                    v-for=" item in billTypes"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </li>


              <li>
                <label>增值类型：</label>
                <el-select v-model="params.valueAddedType">
                  <el-option
                    v-for=" item in valueAddedTypeOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </li>

              <li>
                <label>目的地省：</label>
                <sc-province v-model="params.dispProvId" class="input-h-85"/>
              </li>

              <li>
                <label class="w-r-65">客户：</label>
                <sc-users type="3" v-model="params.customerId" class="input-h-85"/>
              </li>

              <li>
                <label class="w-r-65 ">业务员：</label>
                <sc-users type="2" v-model="params.scanUserId" class="input-h-85"/>
              </li>

              <li>
                <label class="w-r-65">结算重量：</label>
                <div class="weight" style="display: flex;">
                  <el-input @blur="weightFmt('accountWeightBegin')" class="weight1"
                            v-model.trim="params.accountWeightBegin">
                    <template slot="append">≤W≤</template>
                  </el-input>
                  <el-input @blur="weightFmt('accountWeightEnd')" class="weight2"
                            v-model.trim="params.accountWeightEnd"/>
                </div>
              </li>

              <li>
                <label>是否按超票数占比外算费：</label>
                <el-select v-model="params.proportion">
                  <el-option
                    v-for=" item in proportions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </li>


            </ul>
            <div style="float: right;padding-top: 5px;">
              <el-button @click="query" type="primary" class="m-b-16" icon="el-icon-search">查询</el-button>
              <el-button type="danger" class="m-b-16" icon="el-icon-refresh" @click="reset">重置</el-button>
            </div>
          </div>

        </div>

      </div>
    </show-more>
    <div class="sz-search-btn" style="border-bottom:0;">
      <div>
        <el-button @click="onExport" type="primary" icon="el-icon-download">导出</el-button>
      </div>
    </div>

    <!--表格-->
    <div class="sz-content" :style="{height:tableH+'px'}">
      <spread :ngData="tableData" :headData="tableHead"/>
    </div>
    <div class="sz-pagination-box">
      <el-pagination class="pagination"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page.sync="params.currentPage"
                     :page-sizes="[100, 200, 500, 1000,5000]"
                     :page-size="params.pageSize"
                     :total="params.total"
                     v-show="spreade"
                     layout="total, sizes, prev, pager, next, jumper"/>
    </div>
    <!-- 导出汇总 -->
    <sc-csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"
    />
    <async-export
      ref="asyncExport"
      :exportData="tableHead.columns"
      :exportName="exportObj.fileName"
      :exportUrl="exportUrl"
      :exportOption="optionsClone"
    />
  </div>
</template>
<script>
  import {pagerMixin} from "@/utils/mixin";
  import {cloneDeep, trim} from "lodash";
  import moment from 'moment';
  import {queryAvgDetail} from '@/config/api.gateway';
  import {billUtils} from "@zto_module/shenzhou-components-static";

  export default {
    mixins: [pagerMixin],
    data() {
      return {
        spreade: true,
        billCode: "",
        exportUrl: '/bills/avg/export',
        exportObj: {
          fileName: "均重账单计算-单号明细"
        },
        params: {
          bagCodes: '',
          type: '', // 账单类型
          accountUserId: '',
          billType: -1, // 面单类型
          valueAddedType: -1, // 增值类型
          dispProvId: '', // 目的地省
          customerId: '', // 客户
          scanUserId: '', // 业务员
          accountWeightBegin: '', // 结算重量开始
          accountWeightEnd: '', // 结算重量结束
          proportion: -1, // 是否按超出票数占比外算费
          // 翻页
          total: 0,
          pageSize: 100,
          currentPage: 1,
        },
        accountUserName: '', // 结算对象
        billType: 0,
        multipleSelection: [],
        tableHead: {
          columns: [
            {
              displayName: "运单号",
              name: "billCode",
              size: 150,
              isCode: true,
              dblclick: row => {
                window.open(this.z_param.billsUrl + row.illCode)
              }
            },
            {
              displayName: "包号",
              name: "bagCode",
              size: 100,
              isCode: true,
            },
            {
              displayName: "账单日期",
              name: "billDate",
              formatter: "yyyy-MM-dd"
            },
            {
              displayName: "客户名称",
              name: "customerName",
            },
            {
              displayName: "客户编码",
              name: "customerCode", isCode: true, value(row) {
                if (row.customerCode === null) return '';
                return row.customerCode;
              }
            },
            {
              displayName: "业务员",
              name: "scanUserName",
            },
            {
              displayName: "业务员工号",
              name: "scanUserCode", isCode: true, value(row) {
                if (row.scanUserCode === null) return '';
                return row.scanUserCode;
              }
            },
            {
              displayName: "账单类型",
              name: "type",
              value: row => this.fmtBill(row)
            },
            {
              displayName: "结算对象",
              name: "accountUserName",
            },
            {
              displayName: "结算对象编码",
              name: "accountUserCode", isCode: true, value(row) {
                if (row.accountUserCode === null) return '';
                return row.accountUserCode;
              }
            },
            {
              displayName: "产品类型",
              name: "productName",
            },
            {
              displayName: "面单类型",
              name: "billType",
              value: row => this.fmtType(row)
            },
            {
              displayName: "增值类型",
              name: "valueAddedType",
              value: row => this.fmtValueAddedType(row)
            },
            {
              displayName: "目的地省",
              name: "dispProvName",
            },
            {
              displayName: "结算重量",
              name: "accountWeight",
              formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "accountWeight")
            },
            {
              displayName: "网点重量",
              name: "recWeight", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "recWeight")
            },
            {
              displayName: "中心重量",
              name: "centerWeight", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "centerWeight")
            },
            {
              displayName: "面单费",
              name: "szBillFee", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "szBillFee")
            },
            {
              displayName: "增值加收费",
              name: "valuesAddFee", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "valuesAddFee")
            },
            {
              displayName: "通票费",
              name: "avgWeightFee", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "avgWeightFee")
            },
            {
              displayName: "加收费",
              name: "avgAddWeightFee", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "avgAddWeightFee")
            },
            {
              displayName: "应收费用",
              name: "payFee", formatter: '0.00',
              showTotal: true,
              value: row => this.fmtNum(row, "payFee")
            },
            {
              displayName: "是否按超票数占比外算费",
              name: "proportion",
              size: 160,
              value: row => this.fmtProportion(row)
            },
            {
              displayName: "问题件标识",
              name: "problemPiecesType",
              value: row => this.fmtProblemPiecesType(row)
            },
            {
              displayName: "退件标识",
              name: "returnStatus",
              value: row => this.fmtReturnStatus(row)
            },
          ]
        },
        tableData: [],
        tableH: 300,
        ishowHeights: 0,
        dateValue: [
          this.moment().subtract(1, "days").format("YYYY-MM-DD"),
          this.moment().format("YYYY-MM-DD")
        ],
        createTasks: false, // 创建任务弹框
        types: [ // 账单类型
          {value: 0, label: "客户"},
          {value: 2, label: "承包区"},
          {value: 4, label: "业务员"},
          {value: 99, label: "下级网点"}],
        billTypes: [ // 面单类型
          {value: -1, label: "全部"},
          {value: 1, label: "星联面单"},
          {value: 2, label: "时效面单"},
          {value: 3, label: "普通面单"},
          {value: 4, label: "电子面单"}],
        valueAddedTypeOptions: [ // 增值类型
          {value: -1, label: "全部"},
          {value: 5, label: "优鲜送"},
          {value: 6, label: "尊享件"},
          {value: 22, label: "当日达"},
          {value: 99, label: "其他"}
        ],
        proportions: [
          {value: -1, label: "全部"},
          {value: 1, label: "是"},
          {value: 2, label: "否"},
        ],
      }
    },
    methods: {
      fmtNum(row, name) {
        if (row[name] !== undefined && row[name] !== null) {
          return row[name].toFixed(2)
        } else return '';
      },
      weightFmt(code) {
        let accountWeight = this.params[code];
        if (accountWeight.indexOf('.') !== -1) this.params[code] = parseFloat(accountWeight.substring(0, accountWeight.indexOf('.') + 3));
        else {
          if (!isNaN(accountWeight) === true && accountWeight !== '') {
            this.params[code] = parseFloat(accountWeight);
          } else this.params[code] = '';
        }
      },
      initialization(row) { // 点击tab跳转
        this.$nextTick(() => {
          this.dateValue = [moment(row.startDate).format('YYYY-MM-DD'), moment(row.endDate).format('YYYY-MM-DD')];
          this.params.type = row.accountUserType;
          this.accountUserName = row.accountUserName + row.accountUserCode;
          this.params.accountUserId = row.accountUserId;
          this.reset();
          this.query();
        })
      },
      toDetailed(row) { // 双击跳转
        console.log(row);
        this.$nextTick(() => {
          this.dateValue = [moment(row.startDate).format('YYYY-MM-DD'), moment(row.endDate).format('YYYY-MM-DD')];
          this.params.type = row.accountUserType;
          this.accountUserName = row.accountUserName + row.accountUserCode;
          this.params.accountUserId = row.accountUserId;
          this.reset();
          this.query()
        })
      },
      query() {
        this.params.currentPage = 1;
        this.params.pageSize = 100;
        this.onQuery();
      },
      onQuery() {
        // 单号包号限制
        let bagBill = this.billCode.replace(/\n/g, ",").split(',').filter(function (s) {
          return s && s.trim();
        });
        console.log(bagBill);
        bagBill.length !== 0 ? this.spreade = false : this.spreade = true;

        if (!billUtils.regRemark(this.billCode)) {
          this.msg("单号不能有特殊符号和表情");
          return;
        }

        if (this.billType === 0) {
          if (bagBill.length > 1000) {
            return this.$message({
              message: '请输入小于1000的单号',
              type: 'warning',
            });
          }
        } else {
          if (bagBill.length > 200) {
            return this.$message({
              message: '请输入小于200的包号',
              type: 'warning',
            });
          }
        }

        if ((this.params.accountWeightBegin < 0 || this.params.accountWeightEnd > 999) || (this.params.accountWeightEnd < 0 || this.params.accountWeightEnd > 999)) {
          return this.$message({
            message: '重量输入范围[0，999]，请重新输入查询',
            type: 'warning',
          });
        }
        if (this.params.accountWeightBegin > this.params.accountWeightEnd) {
          return this.$message({
            message: '结算重量必须大于等于开始重量哦',
            type: 'warning',
          });
        }

        const {type, accountUserId, billType, valueAddedType, dispProvId, customerId, scanUserId, accountWeightBegin, accountWeightEnd, proportion, pageSize, currentPage} = this.params;

        let params = {
          billDateBegin: moment(this.dateValue[0]).format('YYYY-MM-DD 00:00:00'),
          billDateEnd: moment(this.dateValue[1]).format('YYYY-MM-DD 00:00:00'),
          type: type,
          accountUserId: accountUserId,
          billType: billType,
          valueAddedType: valueAddedType,
          dispProvId: dispProvId === '' || dispProvId === null ? undefined : dispProvId,
          customerId: customerId === '' || customerId === null ? undefined : customerId,
          scanUserId: scanUserId === '' || scanUserId === null ? undefined : scanUserId,
          accountWeightBegin: accountWeightBegin !== '' ? accountWeightBegin : undefined,
          accountWeightEnd: accountWeightEnd !== '' ? accountWeightEnd : undefined,
          proportion: proportion,
          pageSize,
          currentPage
        };
        console.log(customerId);
        if (this.billType === 0) { // 单号
          params.bagCodesStr = undefined;
          params.billCodsStr = this.billCode !== '' ? this.billCode.replace(/\n/g, ",").replace(/\s*/g, "") : undefined;
        } else {
          params.billCodsStr = undefined;
          params.bagCodesStr = this.billCode !== '' ? this.billCode.replace(/\n/g, ",").replace(/\s*/g, "") : undefined;
        }
        this.optionsClone = cloneDeep(params);
        this.optionsClone.siteId = this.z_param.siteInfo.id;
        queryAvgDetail(params).then(res => {
          if (res.result.list.length === 0) {
            this.tableData = [];
            this.params.total = 0;
            this.params.currentPage = 1;
            this.params.pageSize = 100;
            return this.$message({
              message: '查询结果无数据',
              type: 'warning',
            });
          }

          let data = res.result;
          this.tableData = data.list;
          this.params.total = data.totalRow;
        }).catch(err => {
          this.tableData = [];
          this.params.total = 0;
          this.params.currentPage = 1;
          this.params.pageSize = 100;
        })
      },
      tasksSub() {
        if (moment(moment(this.taskParams.endDate).format('YYYY-MM-DD')).diff(moment(moment(this.taskParams.startDate).format('YYYY-MM-DD')), 'days') < 0) {
          return this.$message({
            message: '创建失败，开始日期必须小于等于结束日期',
            type: 'warning'
          });
        }
        this.createTasks = false;
      },
      openTasks() {
        this.createTasks = true;
      },
      handleSelectionChange(val) { // 多选
        this.multipleSelection = val;
        console.log(val)
      },
      handleSizeChange(size) {
        this.params.pageSize = size;
        this.handleCurrentChange(1);
      },
      handleCurrentChange(curPage) {
        if (curPage > 10) return this.$message({
          message: '分页太多啦!请直接下载或精确查询条件!',
          type: 'warning',
        });

        this.params.currentPage = curPage;
        this.onQuery();
      },
      // 切换账单类型 0：单号  1：包号
      getType(flag) {
        this.billType = flag ? 0 : 1;
      },
      keyup() {
        this.billCode = this.billCode.replace(/,/g, "").replace(/，/g, "");
      },
      reset() {
        this.$refs.billcodeRef.type = -1;
        this.billType = 0;
        this.billCode = '';
        this.params.billType = -1; // 面单类型
        this.params.valueAddedType = -1; // 增值类型
        this.params.dispProvId = ''; // 目的地省
        this.params.customerId = ''; // 客户
        this.params.scanUserId = ''; // 业务员
        this.params.accountWeightBegin = ''; // 结算重量开始
        this.params.accountWeightEnd = ''; // 结算重量结束
        this.params.proportion = -1; // 是否按超出票数占比外算费

      },
      billDate(row) {
        if (row.billDate)
          return moment(row.billDate).format('YYYY-MM-DD');
        else return '';
      },
      fmtBill(row) {
        let type = row.type;
        switch (type) {
          case 0:
            return '客户';
          case 2:
            return '承包区';
          case 4:
            return '业务员';
          case 99:
            return '下级网点';
          default:
            return '';
        }
      },
      fmtType(row) {
        if (row.billType) {
          let type = row.billType;
          switch (type) {
            case -1:
              return '全部';
            case 1:
              return '星联面单';
            case 2:
              return '时效面单';
            case 3:
              return '普通面单';
            case 4:
              return '电子面单';
          }
        } else return '';
      },
      fmtValueAddedType(row) {
        if (row.valueAddedType) {
          let type = row.valueAddedType;
          switch (type) {
            case -1:
              return '全部';
            case 5:
              return '优鲜送';
            case 6:
              return '尊享件';
            case 22:
              return '当日达';
            case 99:
              return '其他';
          }
        } else return '';
      },
      fmtProportion(row) {
        if (row.proportion) {
          let proportion = row.proportion;
          switch (proportion) {
            case -1:
              return '全部';
            case 1:
              return '是';
            case 2:
              return '否';
          }
        } else return '';
      },
      fmtProblemPiecesType(row) {
        return row.problemPiecesType ? '是' : '否'
      },
      fmtReturnStatus(row) {
        return row.returnStatus ? '是' : '否'
      },
      onresize() {
        this.tableH = document.body.clientHeight - 280; // 根据用户页面布局修改相应高度
        this.z_method.refreshSpread(); // 刷新表格
      },
      // 导出
      onExport() {
        if (this.tableData.length === 0) {
          this.$message({
            message: '没有数据无法导出!',
            type: 'warning'
          });
          return;
        }
        if (this.billCode) {
          this.checklistData = this.tableData;
          this.checklistExport();
        } else {
          if (this.params.total > this.params.pageSize) {
            if (this.pagination.total > 800000) {
              this.$message({
                message: '最大只支持导出80万，请选择更精准的查询条件!',
                type: 'warning'
              });
            } else {
              this.$refs.asyncExport.export();
            }
          } else {
            this.checklistData = this.tableData;
            this.checklistExport();
          }
        }
      }
    },
    watch: {
      ishowHeights: {
        handler(val) {
          this.tableH = document.body.clientHeight - 300 + val; // 根据用户页面布局修改相应高度
        },
      },
    },
    computed: {
      isCreate() {
        if (this.taskParams.settlement && this.taskParams.startDate && this.taskParams.endDate) return false;
        return true;
      },
      isDel() {
        if (this.multipleSelection.length !== 0) return false;
        else return true;
      }
    },
    mounted() {
      this.$nextTick(() => {
        document.querySelectorAll('.has-gutter .cell')[0].innerText = '选择'
        // this.z_method.refreshSpread(); // 刷新表格
      });
      window.addEventListener('resize', this.onresize);
    },
    created() {
      this.tableH = document.body.clientHeight - 280; // 根据用户页面布局修改相应高度
    },
    destroyed() {
      window.removeEventListener('resize', this.onresize);
    },
  };
</script>
<style scoped lang="scss">
  /deep/ .el-input-group__append {
    border-right: 0;
  }

  /deep/ .weight1 .el-input__inner {
    width: 80px !important;
  }

  /deep/ .weight2 {
    .el-input__inner {
      width: 80px !important;
      border-top-left-radius: 0px !important;
      border-bottom-left-radius: 0px !important;
    }
  }

  /deep/ .el-pagination__jump {
    display: none !important;
  }

  /deep/ .el-input-group__append {
    height: 32px;
  }

  /deep/ th .el-checkbox {
    display: none !important;
  }

  /deep/ .el-table__row td {
    height: 48px;
    border-bottom: 1px solid #CCCCCC;
    border-right: 1px solid #CCCCCC;
  }

  /deep/ .el-table__row td:first-child {
    border-left: 1px solid #CCCCCC;
  }

  /deep/ .el-table__row td:last-child {
    border-right: 1px solid #CCCCCC;
  }

  /deep/ .has-gutter tr {
    height: 50px;
    .is-leaf {
      background: #EAF1F9;
      border-bottom: 1px solid #CCCCCC;
      border-right: 1px solid #CCCCCC;
      border-top: 1px solid #CCCCCC;
    }
    .is-leaf:first-child {
      border-left: 1px solid #CCCCCC;
    }
    .is-leaf:last-child {
      border-right: 1px solid #CCCCCC;
    }
  }

  /deep/ .el-dialog__header {
    background: white !important;
  }

  /deep/ .el-dialog__body {
    background: white !important;

  }

  /deep/ .el-dialog__footer {
    background: white !important;
  }

  .tasks { // 弹框
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background: white;

    div {
