import {get, post, postJson, del, put} from './axios.config';

// 均重账单计算-均重计算
export const createTasks = param => post('/avg/claTaskIndex/addClaTask', param, false); // 新增计算任务
export const queryClaTasks = param => postJson('/avg/claTaskIndex/queryClaTasks', param); // 查询计算任务
export const judgeIsCalculOver = param => post('/avg/claTaskIndex/judgeIsCalculOver', param); // 判断是否 计算出均重费用
export const executeCalStep1 = param => post('/avg/claTaskIndex/executeCalStep1', param, false); // 重新计算 第一步 报价信息校验
export const executeCalStep2 = param => post('/avg/claTaskIndex/executeCalStep2', param); // 确认重新计算
export const deleteTask = param => post('/avg/claTaskIndex/deleteTask', param); // 删除计算任务
// 结果总览
export const queryAvgDayCount = param => post('/avg/preview/queryAvgDayCount', param); // 表格1
export const queryCalSnapShot = param => post('/avg/preview/queryCalSnapShot', param); // 表格2、3
// 单号明细
export const queryAvgDetail = param => post('/avg/preview/queryAvgDetail', param, ); // 查询
